.faq {
    padding: 40px 0 60px;
    &__item {
        position: relative;
        background-color: var(--White);
        border: 1px solid var(--iconColor);
        padding: 25px 130px 27px 30px;
        margin-bottom: 10px;
        transition: all .2s;
        cursor: pointer;
        &::after {
            position: absolute;
            top: 27px;
            right: 35px;
            font-family: "icons";
            font-size: 17px;
            content: "\E005";
            transform: rotate(90deg);
            transition: all .2s;
        }
        h5 {
            margin: 0;
            font-weight: 500;
            transition: all .2s;
        }
        &-answer {
            display: none;
            margin-top: 15px;
        }
        &:hover {
            position: relative;
            box-shadow: 0px 6px 18px 0px rgba(0, 0, 0, .2);
            h5 {
                font-weight: 700;
            }
        }
        &-hovered {
            box-shadow: 0px 6px 18px 0px rgba(0, 0, 0, .2);
            h5 {
                font-weight: 700;
            }
            &::after {
                transform: rotate(-90deg);
            }
        }
    }
}
@media(max-width: 575px) {
    .faq {
        &__item {
            padding: 25px 35px 27px 20px;
            &::after {
                right: 20px;
            }
        }
    }
}
